const { React, ReactDOM } = window;

const wait = ms => new Promise(resolve => setTimeout(resolve, ms));

class App extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      counter: 0,
      normalCounter: 0,
    };
  }

  componentDidMount() {
    this.update();
    this.normalUpdate();
  }

  normalUpdate() {
    this.setState({
      normalCounter: this.state.normalCounter + 1,
    });
    this.setState({
      normalCounter: this.state.normalCounter + 1,
    });
    setTimeout(() => {
      this.setState({
        normalCounter: this.state.normalCounter + 1,
      });
      this.setState({
        normalCounter: this.state.normalCounter + 1,
      });
    }, 1000);
  }

  async update() {
    this.setState({
      counter: this.state.counter + 1,
    });
    this.setState({
      counter: this.state.counter + 1,
    });
    await wait(1000);
    this.setState({
      counter: this.state.counter + 1,
    });
    this.setState({
      counter: this.state.counter + 1,
    });
  }

  render() {
    return (
      <div>
        <p>NormalCounter: {this.state.normalCounter}</p>
        <p>Counter: {this.state.counter}</p>
      </div>
    );
  }
}

ReactDOM.render(<App />, document.getElementById('app'));
